@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/montserrat-v14-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-v14-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-700.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  src: local("Montserrat ExtraBold"), local("Montserrat-ExtraBold"), url("../fonts/montserrat-v14-latin_cyrillic-800.woff2") format("woff2"), url("../fonts/montserrat-v14-latin_cyrillic-800.woff") format("woff"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 400;
  src: local("Fira Sans Regular"), local("FiraSans-Regular"), url("../fonts/fira-sans-v10-latin_cyrillic-regular.woff2") format("woff2"), url("../fonts/fira-sans-v10-latin_cyrillic-regular.woff") format("woff"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 700;
  src: local("Fira Sans Bold"), local("FiraSans-Bold"), url("../fonts/fira-sans-v10-latin_cyrillic-700.woff2") format("woff2"), url("../fonts/fira-sans-v10-latin_cyrillic-700.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  src: local("Raleway ExtraBold"), local("Raleway-ExtraBold"), url("../fonts/raleway-v3-latin_cyrillic-800.woff2") format("woff2"), url("../fonts/raleway-v3-latin_cyrillic-800.woff") format("woff"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 900;
  src: local("Raleway Black"), local("Raleway-Black"), url("../fonts/raleway-v3-latin_cyrillic-900.woff2") format("woff2"), url("../fonts/raleway-v3-latin_cyrillic-900.woff") format("woff"); }

body {
  margin: 0;
  padding: 0;
  font-family: "Fira Sans", sans-serif; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
p {
  margin: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

h1 {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 200px;
  line-height: 170%;
  letter-spacing: 0.055em;
  text-transform: uppercase; }

h2 {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 49px;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  color: #1fc0fc; }

h3 {
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  letter-spacing: -0.02em;
  color: #484848; }

h4 {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 800;
  font-size: 21px;
  line-height: 25px;
  color: #484848; }

@media (max-width: 767px) {
  h1 {
    font-weight: 800;
    font-size: 72px; }
  h2 {
    font-size: 32px;
    line-height: 39px; }
  h3 {
    font-size: 24px;
    line-height: 29px; }
  h4 {
    font-size: 18px;
    line-height: 21px; } }
